.container {
  display: flex;
  background-color: #ccc;
  padding: 0.5rem;
  border-radius: 10px;
  min-height: calc(100vh - 1rem);
}

.aside {
  flex-grow: 1;
  border-radius: 5px;
  background-color: #fff;
  margin: 0.5rem;
  flex-basis: 25%;
}

.main {
  flex-grow: 3;
  margin-left: 0.5rem;
  border-radius: 5px;
  background-color: #fff;
  margin: 0.5rem;
}

.title {
  font-style: italic;
  text-align: left;
  margin-left: 1rem;
}

.main-text {
  padding: 0.5rem;
}

