body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.wrapper {
  padding: 1rem;  
}

.wrap {
  display: flex;
}

.button-legal {
  border: 2px solid #a7dbe5;
  border-radius: 50%;
  min-width: 50px;
  height: 50px;
  color: #a7dbe5;
  font-size: 2rem;
  background-color: #fff;
}

.button-wrapper {
  display: flex;
}

.body-button {
  border: none;
  background-color: #fff;
  font-style: italic;
  padding: 1rem 2rem;
  font-size: 1.2rem;
  margin-left: calc(50px + 2rem);
}

.title-legal {
  margin: 0;
  margin-left: 1rem;
}

.flag {
  display: inline;
  font-size: 2rem;
  color: #a7dbe5;
  font-weight: bold;
  margin-left: 1rem;
  font-family: serif;
}

.button-fop {
    border: none;
    background-color: #fff;
    font-style: italic;
    font-weight: bold;
    font-size: 1.2rem;
    text-align: left;
    margin-left: 2rem;
    
}
.input {
  margin-left: 1rem;
  margin-right: 1rem;
  border: 2px #6b6b6b solid;
  border-radius: 25px;
  padding-left: 0.5rem;
  margin-bottom: 1rem;
}

.label {
  font-style: italic;
  font-weight: 700;
  margin-left: 0.5rem;
  vertical-align: middle;
}

.input__title {
  font-style: italic;
  font-weight: 700;

}

.row__wrapper {
  display: flex;
  align-items: center;
}

.row__wrapper--end {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.minjust-link {
  color: #1596cd;
  display: block;
  border: 3px #1596cd solid;
  text-decoration: none;
  font-weight: 700;
  font-style: italic;
  border-radius: 25px;
  padding: 0 2rem;
  margin-left: 10px;
}

.wrapper__main {
  padding: 1rem;
}

.submit {
  color: #1596cd;
  border: 3px #1596cd solid;
  font-weight: 700;
  font-style: italic;
  border-radius: 25px;
  padding: 0.5rem 2rem;
  background-color: #fff;
  cursor: pointer;  
}

.KVED_option__UQYpX {
  width: 350px;
  margin: 16px; }

.legal__menu {
  display: flex;
  list-style: none;
  justify-content: space-around;
}

.menu__link {
  display: block;
  background-color: #dee1e6;
  padding: 0.2rem 2rem;
  text-transform: uppercase;
  font-weight: bold;
  text-decoration: none;
  border-radius: 10px;
  color: #fff;
}

.menu__link--active {
  background-color: #b4b7bc;
  color: #cccdcf;
}

.container {
  display: flex;
  background-color: #ccc;
  padding: 0.5rem;
  border-radius: 10px;
  min-height: calc(100vh - 1rem);
}

.aside {
  flex-grow: 1;
  border-radius: 5px;
  background-color: #fff;
  margin: 0.5rem;
  flex-basis: 25%;
}

.main {
  flex-grow: 3;
  margin-left: 0.5rem;
  border-radius: 5px;
  background-color: #fff;
  margin: 0.5rem;
}

.title {
  font-style: italic;
  text-align: left;
  margin-left: 1rem;
}

.main-text {
  padding: 0.5rem;
}


.App {
  /* text-align: center; */
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  /* min-height: 100vh; */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

body {
  margin: 0;
  padding: 1.5em;
  font-family: Georgia, Utopia, Charter, serif;
}


input[type=checkbox]:not(old),
input[type=radio]:not(old) {
  width: 2em;
  margin: 0;
  padding: 0;
  font-size: 1em;
  opacity: 0;
}

input[type=checkbox]:not(old)+label,
input[type=radio]:not(old)+label {
  display: inline-block;
  margin-left: -2em;
  line-height: 1.5em;
}

input[type=checkbox]:not(old)+label>span,
input[type=radio]:not(old)+label>span {
  display: inline-block;
  width: 0.875em;
  height: 0.875em;
  margin: 0.25em 0.5em 0.25em 0.25em;
  border: 0.0625em solid rgb(192, 192, 192);
  border-radius: 0.25em;
  background: rgb(224, 224, 224);
  background-image: -webkit-linear-gradient(rgb(240, 240, 240), rgb(224, 224, 224));
  background-image: linear-gradient(rgb(240, 240, 240), rgb(224, 224, 224));
  vertical-align: bottom;
}

input[type=checkbox]:not(old):checked+label>span,
input[type=radio]:not(old):checked+label>span {
  background-image: -webkit-linear-gradient(rgb(224, 224, 224), rgb(240, 240, 240));
  background-image: linear-gradient(rgb(224, 224, 224), rgb(240, 240, 240));
}

input[type=checkbox]:not(old):checked+label>span:before {
  content: '\2713';
  display: block;
  width: 1em;
  color: rgb(153, 204, 102);
  font-size: 0.875em;
  line-height: 1em;
  text-align: center;
  text-shadow: 0 0 0.0714em rgb(115, 153, 77);
  font-weight: bold;
}

input[type=radio]:not(old):checked+label>span>span {
  display: block;
  width: 0.5em;
  height: 0.5em;
  margin: 0.125em;
  border: 0.0625em solid rgb(115, 153, 77);
  border-radius: 0.125em;
  background: rgb(153, 204, 102);
  background-image: -webkit-linear-gradient(rgb(179, 217, 140), rgb(153, 204, 102));
  background-image: linear-gradient(rgb(179, 217, 140), rgb(153, 204, 102));
}

label {
  margin-right: 1rem;
}
